<template>
    <div class="foodcart">
        <!-- 购物车图片 -->
        <div class="imgs-bg">
            <div class="imgs">
                <!-- 点击事件 -->
                <img src="/static/imgs/shopping.png" alt="" @click="showBuyDetail">
            </div>
        </div>
        <!-- 消费 -->
        <div class="consume">
            <p>￥0</p>
        </div>
        <!-- 配送费 -->
        <div class="dispatching">
            <p>另需配送费</p>
            <span>￥4元</span>
        </div>
        <!-- 起送费 -->
        <div class="Send">
            <p>￥20元起送</p>
        </div>
         <buydetails></buydetails>
    </div>
</template>

<script>
    import { mapGetters } from 'vuex'
    import buydetails from '../buydetail/buydetail.vue'
    export default {
        methods:{
			showBuyDetail( ){
				this.$store.dispatch('showBuyDetail');
			}
        },
        components:{
          buydetails
        }
    }
</script>

<style lang="less" scoped>
    .foodcart{
        width: 100%;
        height: 1.16rem;
        background: #444;
        display: flex;
        background-color: #141d27;
        z-index:66;
        // 购物车图片
        .imgs-bg{
            width: 1.1rem;
            height: 1.1rem;
            display: flex;
            align-items: center;
            justify-content: center;        
            background-color: #141d27;
            // border: 1px solid rebeccapurple;
            border-radius: 1rem;
            margin: -0.2rem 0.36rem 0.16rem 0.36rem;
            .imgs{
                display: flex;
                align-items: center;
                justify-content: center;
                width: 0.88rem;
                height: 0.88rem;
                
                // line-height: 0.6rem;
                background: rgba(255,255,255,0.2);
                border-radius: 0.8rem;
                margin: 0 auto;
                img{
                    width: 0.5rem;
                    height: 0.5rem;
                }
            }
        }    
        // 消费 
        .consume{
            color:rgba(255,255,255,0.4);
            width: 0.75rem;
            height: 50%;
            margin-top: 0.28rem;
            margin-right: 0.24rem;
            border-right: 0.02rem solid rgba(255,255,255,0.1);
            p{
                font-size: 0.32rem;
                margin-top: 0.1rem;
            }
        }     
        // 配送费   
        .dispatching{
            color:rgba(255,255,255,0.4);
            width: 3rem;
            height: 100%;
            text-align: left;
            p{
                display: inline-block;
                margin-top: 0.44rem;
                font-size: 0.24rem;
            }
        }   
        // 起送费  
        .Send{
            color:rgba(255,255,255,0.4);
            width: 2.1rem;
            height: 100%;
            // text-align: center;
            background-color: #555;
            p{
                margin-top: 0.45rem;
                margin-left: 0.24rem;
                font-size: 0.24rem;
            }
        }
    }
</style>



