<template>
	<div class="cartControl">
		<div class="reduce" @click.stop='reduce' v-show="food.count>0">
            <img src="/static/imgs/reduces.png" alt="">
        </div>
		<div class="count" v-show="food.count>0">{{food.count}}</div>
		<div class="add" @click.stop='add'>
            <img src="/static/imgs/adds.png" alt="">
        </div>
	</div> 
</template>

<script>
	import Vue from 'vue'

	export default{

		props:['food'],
		methods:{
			add(){
				if( !this.food.count ){
					Vue.set(this.food , 'count' , 1);
				}else{
					this.food.count ++;
				}
			},
			reduce(){
				if( this.food.count ){
					this.food.count --;
				}
			}
		}
	}
</script>

<style lang='less' scoped>
	
	.cartControl{
		width: 1.5rem;
		height: 0.5rem;
		display: flex;
		
		div{
			flex:1;
			width: 0.5rem;
			height: 0.5rem;
			// border: 1px solid red;
			line-height: 0.5rem;
			text-align: center;
        }
        .add{
            img{
                background-color: #1296db;
                border-radius: 0.4rem;
                width:0.4rem;
                height: 0.4rem;
                
            }
        }
        .reduce{
            img{
                border-radius: 0.4rem;
                width:0.4rem;
                height: 0.4rem;
            }
        }
	}
</style>


