<template>
	<div class="goods" >
		<div class="menu-wrap" ref="menuwrap">
			<ul>
				<!-- {{currentIndex}} -->
				<li @click='selectMenu(i)' :class='{"current" : currentIndex == i }' class="menu-item" v-for='(val,i) in goods' :key='i'>
					<span class="text">
						{{val.name}}
					</span>
				</li>
			</ul>
		</div>
		<div class="foods-wrap" ref='foodwrap'>
			<ul>
				<li :key="i" v-for='(item,i) in goods' class="food-list food-list-hook">
							<!-- 菜品系列 77777777返类-->
						<div class="typeof">
							<div class="typeofone"></div>
							<p>{{ item.name }}</p>
						</div>
						<!-- 菜品系列end++++ -->
						<ul>
						<!-- 循环菜品详情 -->
							<li :key="i" v-for = '(food,i) in item.foods' class="food-item" @click='showFoodDetail(food)'>
								<!-- <li class="food-item"> -->
								<!-- 菜单+图片+名称部分 -->
								<div class="contents">
											<!-- 左边的图片菜品 -->
											<div class="contentleft">
												<img :src="food.image" alt="">
											</div>
											<!-- 图片菜品+++ -->

											<!-- 右边的名称月销售量 -->
											<div class="contentright">
													<div class="name">{{food.name}}</div>
													<div class="namelittle">{{food.description}}</div>
													<div class="monthsell">
														月售<span>{{food.sellCount}}</span>
														份
													</div>
													<div class="monthrating">
														好评率&nbsp;<span>{{food.rating}}</span>
														%
													</div><br>
													<div class="priceone">￥<span>{{food.price}}</span></div>
													<div class="pricetwo">￥<span>{{food.oldPrice}}</span></div>
													<div class="daaandreduce">
														<carcontrol class='controlcar' :food='food'></carcontrol>
													</div>
											</div>
											<!-- 右边的名称月销售量+++ -->
								</div>
								<!-- 菜单+图片+名称部分end++++++ -->
							</li>
					</ul>
					<!-- 结束循环菜品详情 -->
				</li>
			</ul>
		</div>
		 <!-- 购物车固定在底部 -->
       <div class="shopcar">
           <shopCar></shopCar>
       </div>
       <!-- 购物车固定在底部end+++++++ -->
		<foodDetail></foodDetail>
	</div>
</template>

<script>
	import { mapGetters } from 'vuex'
	import BScroll from 'better-scroll'
	import foodDetail from '../foodDetail/foodDetail.vue'
	import shopCar from '../shopcar/shopcar.vue'
	import carcontrol from '../carcontrol/carcontrol.vue'

	export default{
		components:{
			foodDetail,
			shopCar,
			carcontrol
		},
		computed:{
			...mapGetters([
				'getters',
				'goods',
				'ratings'
			]),
				// 计算当前位置在哪一区间内 然后返回当前i
			currentIndex(){
				//查看当前定义的数组里面的值--Y轴方向的高度
				// console.log(this.heights);
				for( let i = 0 ; i < this.heights.length ; i++){
					let height1	= this.heights[i];
					let height2	= this.heights[i+1];
					//如果Y轴的高度在当前数组内两个高度之间话，就返回i
					if( this.scrollY >= height1 && this.scrollY < height2){
						return i;
					}
				}
				return 0;
			}
		},
		data(){
			return {
				// cc : 1469281964000,
				heights:[],
				scrollY : ''
			}
		},
		mounted(){
			//在seller.vue里面定义了滚动事件，此处调用了该方法并执行
			this.$nextTick( () => {
				this.initBScroll();
				this.calcHeight();
			})
		},
		methods:{
			initBScroll(){
				// 右侧滚动
				if( !this.foodwrapBScroll ){
					this.foodwrapBScroll = new BScroll( this.$refs.foodwrap,{
						click : true,
						probeType : 3
					})
				}else{
					this.foodwrapBScroll.refresh();
				};

				// 左侧滚动
				if( !this.menuwrapBScroll ){
					// console.log(0);
					this.menuwrapBScroll = new BScroll( this.$refs.menuwrap,{
						click : true,
						probeType:3
					})
				}else{
					this.menuwrapBScroll.refresh();
				};				
				this.foodwrapBScroll.on('scroll', pos => {
					// console.log(pos);
					this.scrollY = Math.abs( Math.round( pos.y));
					// console.log(this.scrollY);
				}) 
			},
			calcHeight(){
				let foodList = document.getElementsByClassName('food-list-hook');
				let height = 0 ;
				this.heights.push( height );
				for(let i = 0 ; i < foodList.length ; i++){
					//获取每一个li（区块）的高度
					height += foodList[i].clientHeight;
					this.heights.push( height );
				}
				// [0, 599, 684, 769, 968, 1110, 1252, 1451, 1765, 2136]
				// console.log(this.heights);

			},
			// 左侧点击事件
			selectMenu(i){
				// console.log(i)
				let foodList = document.getElementsByClassName('food-list-hook');
				this.foodwrapBScroll.scrollToElement(foodList[i],300);
			},
			showFoodDetail( f ){
				this.$store.dispatch('showFoodDetail',f);
			}
		}

	}
</script>

<style lang='less' scoped>
	.goods{
		position: absolute;
		display: flex;
		overflow: hidden;
		width: 100%;
		top:3.5rem;
		bottom: 1rem;
		.menu-wrap{
			/* 不扩展 不缩放 */
			flex: 0 0 1.6rem;
			width: 1.6rem;
			.menu-item{
				&.current{
					background-color: white;
				}
				width: 1.12rem;
				height: 1.08rem;
				font-size: 0.24rem;
				background: #f3f5f7;
				color:rgb(7,17,27);
				padding: 0 0.24rem;
				font-weight: 200;
				line-height: 0.28rem;
				display: table;
				.text{
					width: 1.12rem;
					display: table-cell;
					vertical-align: middle;
					border-bottom: 1px solid rgba(7,17,27,0.1);
				}
			}

		}
		.foods-wrap{
			flex:1;
			.food-list{
				.typeof{
					margin-left:-0.02rem;
					width:100%;
					height:0.52rem;
					background-color: #f3f5f7;
						div{
							display:inline-block;
							background-color: #d9dde1;
							width:0.05rem;
							height:100%;
						}
						p{
							display:inline-block;
							vertical-align:top;
							margin-left:0.23rem;
							font-size: 0.24rem;
							color:rgb(147,153,159);
							line-height:0.52rem;
						}
				}
				ul{
					.food-item{
						width: 100%;
						height:2.4rem;
						// border: 1px solid #000;						
						line-height: 1rem;
						.contents{
								border-bottom:1px solid rgba(7,17,27,0.1);
								box-sizing: border-box;
								display:inline-block;
								width:100%;
								height:2.4rem;
								display:flex;
								.contentleft{
									// border:1px solid red;
									box-sizing: border-box;
									flex:1;
									width:1.1rem;
										img{
										display:inline-block;
										margin-top:0.36rem;
										margin-left:0.3rem;
										width:1.3rem;
										height:1.3rem;
										}
								}
								.contentright{
									// border:1px solid green;
									box-sizing: border-box;
									flex:1.7;
									.name{
										// border:1px solid red;
										width:2.5rem;
										height:0.28rem;
										font-size:0.28rem;
										color:rgb(7,17,27);
										line-height:0.28rem;
										margin-top:0.36rem; 
										text-overflow: ellipsis;
										white-space: nowrap;
										overflow: hidden;
									}
									.namelittle{
										width:1.2rem;
										height:0.4rem;
										// border:1px solid red;
										line-height: 0.2rem;
										margin-top: 0.16rem;
										font-size: 0.2rem;
										color:rgb(147,153,159);
										text-overflow: ellipsis;
										white-space: nowrap;
										overflow: hidden;
									}
									.monthsell{
										// border:1px solid red;
										width:1.3rem;
										height:0.4rem;
										line-height: 0.2rem;
										font-size: 0.2rem;
										color:rgb(147,153,159);
										// margin-top:0.2rem;
										display:inline-block;
										vertical-align:top;
									}
									.monthrating{
										// border:1px solid red;
										width:1.5rem;
										line-height: 0.2rem;
										height:0.4rem;
										font-size: 0.2rem;
										color:rgb(147,153,159);
										// margin-top:0.2rem;
										margin-left: 0.24rem;
										display:inline-block;
										vertical-align:top;
									}
									.priceone{
										// border:1px solid red;
										margin-top: -0.4rem;
										display:inline-block;
										vertical-align:top;
										color:red;
										font-size: 0.28rem;
										font-weight: 700;
										line-height: 0.48rem;
										}
										.pricetwo{
											// border:1px solid red;
											margin-top: -0.4rem;
											display:inline-block;
											vertical-align:top;
											color:rgb(147,153,159);
											font-size: 0.2rem;
											font-weight: 700;
											line-height: 0.48rem;
											text-decoration:line-through;
											}
											.daaandreduce{
												display:inline-block;
												vertical-align:top;
												// border:1px solid red;
												width:2rem;
												height:0.5rem;
												line-height:0.5rem;
												margin-top:-0.5rem;
												margin-left:0.3rem;
												.controlcar{
													margin:0 auto;
												}
											}
            							}            
        						}
						}
				}
			}
		}
		.shopcar{
            // border:1px solid red;
            position: fixed;
            bottom:0;
            left:0px;
            width:7.5rem;
            height:1rem;
           
        }
	}
</style>


















