import axios from 'axios';
import TYPES from '../types.js'

const state = {
	goods : {},
	detailShow : false,
	buyDetailShow:false,
	food : {}
}

const getters = {
	ccc(state){
		return state.ccc;
	},
	goods(state){
		return state.goods;
	},
	
	detailShow( state ){
		return state.detailShow;
	},
	food( state ){
		return state.food;
	},
	buyDetailShow(state){
		return state.buyDetailShow;
	}
}
const actions = {
	getGoods( {commit} ){
		axios.get('data.json').then( res => {
			commit( TYPES.GET_GOODS , res.data.goods );
		})
	},
	showFoodDetail( { commit } , food ){
		commit( TYPES.SHOW_FOODDETAIL ,food );
	},
	hideFoodsDetail( { commit }){
		commit( TYPES.HIDE_FOODSDETAIL);
	},
	showBuyDetail({commit}){
		commit(TYPES.SHOW_BUYDETAIL);
	}
}
const mutations = {
	[TYPES.GET_GOODS](state,data){
		console.log(data);
		state.goods = data;
	},
	[TYPES.SHOW_FOODDETAIL](state,food){
		state.detailShow = true;
		state.food = food;
	},
	[TYPES.HIDE_FOODSDETAIL](state){
		// console.log('我是要隐藏');
		state.detailShow = false;
	},
	[TYPES.SHOW_BUYDETAIL](state){
		console.log('我显示购物车');
		state.buyDetailShow = true;
	}
}

export default {
	state,
	getters,
	mutations,
	actions
}


