<template>
	<transition enter-active-class = 'animated slideInDown' leave-active-class = 'animated rollOut'>
		
			<div class="sellerDetail" v-show='sellerShow'>
				<!-- 整个页面 -->
				<div class="hides" @click='hideDetail'>
					<!-- 第一部分 -->
					<div class="tops">
						<div class="contentone">
							<!-- 文字 -->
							<p>{{seller.name}}</p>
							<!-- 星星 -->
							<div class="star">
								<Star :score='seller.serviceScore' :size="'item'" class="littlestar"></Star>
							</div>
						</div>
						<!-- 横县中间夹着文字部分 -->
						<div class="hrs">
							<hr>
							<p>优惠信息</p>
							<hr>
						</div>
					</div>
					
					<!-- 第二部分 -->
					<div class="mids">
						<div class="content" v-for="item in seller.supports">
							<img src="/static/imgs/decrease.png" alt="">
							<div class="content-item">
								<p>{{item.description}}</p>
							</div>
						</div>
					</div>
					<!-- 第三部分 -->
					<div class="bots">
						<div class="hrs">
							<hr>
							<p>商家公告</p>
							<hr>
						</div>
						<div class="texts">{{seller.bulletin}}</div>
					</div>
					<!-- 第四部分 -->
					<div class="closed">
						<img src="/static/imgs/close.png" alt="">
					</div>
				</div>				
			</div>
		
	</transition>
</template>

<script>
	import { mapState,mapGetters } from 'vuex'
	import animate from 'animate.css'
	import Star from '../star/star.vue'

	export default{
		computed: mapGetters([
		  // 映射 this.count 为 store.state.count
		  'seller',
		  'sellerShow'
		]),
		methods:{
			hideDetail(){
				this.$store.dispatch('hideDetail');
			}
		},
		components:{
            Star
            
        }

	}
</script>

<style lang='less' scoped>
	.sellerDetail{
		z-index:15;
		padding-bottom:1rem;;
		width: 100%;
		height: 100%;
		position: fixed;
		left: 0;
		top: 0;
	
		.hides{
			width: 100%;
			height: 100%;
			border: 1px solid #000;
			background-color:rgba(7,17,27,0.8);
		
			.tops{
				box-sizing: border-box;
				// border:0.2px solid black;
				width:100%;
				height: 4rem;
				.contentone{
					// border: 2px solid yellow;
					width:4.8rem;
					height:1.62rem;
					margin:0.58rem auto;
					
					p{
						margin:0 auto;
						text-align: center;
						// border: 1px solid green;
						width:3.8rem;
						height:0.62rem;
						font-size: 0.32rem;
						color:rgb(255,255,255);
						font-weight: 700;
						line-height: 0.32rem;
						margin-top:0.128rem;
						}
						
						.star{
							margin-top:0.1rem;
							text-align: center;
							// border: 1px solid green;
							width:4.8rem;
							height:0.62rem;
							img{
								display: inline-block;
								width: 0.4rem;
								height: 0.4rem;
							}
						}
				}
				.hrs{
						margin:0 auto;
						margin-top:-0.4rem;
						width:6.1rem;
						height: 0.2rem;
						p{
							margin:0 auto;
							margin-top:0rem;
							width:1.3rem;
							vertical-align: center;
							text-align: center;
							color:white;
							font-size:0.28rem;
							font-weight: 700;
							background-color:rgba(7,17,27,0.1); 
							display:inline-block;
						}
						hr{
							color:rgb(255,255,255,0.2);
							display:inline-block;
							width:2.24rem;
						}
					}
				
			}
			.mids{
				box-sizing: border-box;
				// border:1px solid red;
				margin-top:-1.4rem;
				width:85%;
				height: 5rem;					
				.content{
					display:inline-block;
					vertical-align: top;
					// border:1px solid green;
					margin:0 auto;
					width:6rem;
					height:0.9rem;
					text-overflow: ellipsis;
					white-space: nowrap;
					overflow: hidden;
					margin-left:0.3rem;
					img{
						display:inline-block;
						vertical-align: top;
						margin-top:0.37rem;
						width:0.4rem;
						height:0.4rem;
					}
					.content-item{
						display:inline-block;
						// border:1px solid white;
						margin-top:0.2rem;
						width:5.7rem;
						height:0.7rem;
						vertical-align: center;
						// text-overflow: ellipsis;
						// white-space: nowrap;
						// overflow: hidden;
						p{
							display: inline-block;
							// border:1px solid red;
							vertical-align: top;
							font-size: 0.24rem;
							font-weight: 200;
							color:rgb(255,255,255);
							line-height: 0.7rem;
							width:5rem;
							text-overflow: ellipsis;
							white-space: nowrap;
							overflow: hidden;
						}
					}
				}
			}
			.bots{
				box-sizing: border-box;
				// border:1px solid red;
				width:100%;
				height: 4rem;
				.hrs{
						margin:0 auto;
						margin-top:0.12rem;
						width:6.1rem;
						height: 0.2rem;
						// border: 2px solid green;
							p{
								margin:0 auto;
								width:1.3rem;
								vertical-align: center;
								text-align: center;
								color:white;
								font-size:0.28rem;
								font-weight: 700;
								background-color:rgba(7,17,27,0.1); 
								display:inline-block;
								}
							hr{
								color:rgb(255,255,255,0.2);
								display:inline-block;
								width:2.24rem;
							}
					}
					.texts{
						// border: 2px solid green;
						width:5.32rem;
						height:2.8rem;
						line-height: 0.48rem;
						font-size:0.24rem; 
						margin:0 auto;
						margin-top:0.3rem;
						text-overflow: ellipsis;
						// white-space: nowrap;
						overflow: hidden;
						font-weight: 200;
						color:rgb(255,255,255);
						lighting-color: 0.48rem;
					}
			}
			.closed{
				// border: 2px solid green;
				position: fixed;
            	bottom:0;
				width:100%;
				height: 1rem;
				position: relative;
				clear:both;
				font-size: 0.64rem;
				text-align: center;
				img{
					// margin:0 auto;
					margin-top:0.01rem;
					width:0.64rem;
					height:0.64rem;
				}
			}
		}
	}
</style>


