<template>
    <div class="content" id="cc" ref="ccc">
        <div>
       <!-- 商家名称与收藏部分 -->
       <div class="topone">
           <div class="topone-left">
               <!-- 商家名字 -->
               <p class="sellername">{{seller.name}}</p>
               <!-- 星星 -->
               <div class="starts">
                   <Star :score='seller.serviceScore' :size="'small'" class="littlestar"></Star>
               </div>
               <!-- 月销售量 -->
               <p class="total">
                   (
                   <span class="totalcount">{{seller.score}}</span>
                   )
               </p>
               <!-- 月销售量单数 -->
               <p class="monthtotal">
                   月售
                   <span class="monthcount">{{seller.sellCount}}</span>
                   单
               </p>
               
           </div>
           <div class="topone-right">
               <div class="love">
                   <img src="/static/imgs/love.png" alt="">
               </div>
               <div class="tackcare">
                   <span>已收藏</span>
               </div>
           </div>
       </div>
       <!-- 商家名称与收藏部分结束+++ -->

       <!-- 头部第二部分外卖费详情部分 -->
       <div class="toptwo">
           <div class="toptwo-item startprice">
               <!-- 起送价 -->
               <div>起送价</div>
               <p>
                   <span>{{seller.minPrice}}</span>元
               </p>
           </div>
           <div class="toptwo-item deliverprice">
               <!-- 商家配送 -->
               <div>商家配送</div>
               <p>
                   <span>{{seller.deliveryPrice}}</span>元
               </p>
           </div>
           <div class="toptwo-item everytime">
               <!-- 平均配送时间 -->
               <div>平均配送时间</div>
               <p>
                   <span>{{seller.deliveryTime}}</span>分钟
               </p>
           </div>
       </div>
       <!-- 头部第二部分外卖费详情部分结束+++ -->

       <!-- 空白部分1111 ++++++++-->
       <div class="whites1"></div>
       <!-- 空白分 enddd++++-->

       <!-- 公告与活动 -->
       <div class="active">
           <p class="titles">公告与活动</p>
           <p class="textcontent">
               {{seller.bulletin}}               
            </p>
            <!-- 循环的优惠部分 -->
            <div class="turn" v-if='seller.supports' :key='i' v-for="(item,i) in seller.supports">
                <img src="dist/static/imgs/decrease.png" alt="">
                <p>{{item.description}}</p>
            </div>
            <!-- 循环的优惠部分end+++++ -->
       </div>
       <!-- 公告与活动end+++++ -->

       <!-- 空白部分22222 ++++++++-->
       <div class="whites2"></div>
       <!-- 空白分 enddd++++-->

        <!-- 商家实景 -->
           <div class="relpic">
				<h1 class="title">商家实景</h1>
				<div class="pic-wrapper" ref='picwrap'>
					<ul class="pic-list" ref = 'piclist'>
						<li class="pic-item" v-for='pic in seller.pics' :key='pic'>
							<img :src="pic" width="120" height="90">
						</li>
					</ul>
				</div>
			</div>
        <!-- 商家实景 enddd++++-->  

        <!-- 空白部分3333 -->
       <div class="whites3"></div>
       <!-- 空白分 enddd++++-->

       <!-- 商家信息-->
       <div class="sellinfo">
           <div class="infotitle">商家信息</div>
           <!-- 循环商家信息 -->
           <div v-for="(val,key) in seller.infos" :key='key'>
               <p>{{val}}</p>
           </div>
       </div>
       <!-- 商家信息 enddd++++-->   
 
       </div>
        <!-- 购物车固定在底部 -->
       <div class="shopcar">
           <shopCar></shopCar>
       </div>
       <!-- 购物车固定在底部end+++++++ --> 
    </div>
</template>
<script>
    import {mapGetters,mapState} from 'vuex'
    import sellerDetail from '../sellerDetail/sellerDetail.vue'
    import BScroll from 'better-scroll'
    import Star from '../star/star.vue'
    import shopCar from '../shopcar/shopcar.vue'
    export default {
        computed:{
            ...mapGetters([
                'seller'
                 ])
         },
        watch:{
			seller(){
                this._initPicture();
				this.$nextTick( () => {
					if( !this.sellerSCroll ){
						this.sellerSCroll = new BScroll( document.getElementById('cc') ,{
						});
					}else{
						this.sellerSCroll.refresh();
					}
				})
			}
		},
		mounted(){
            this._initPicture();
			this.$nextTick( () => {

                if( !this.sellerSCroll ){
                    
                   this.sellerSCroll = new BScroll( this.$refs.ccc,{
                    });
                }else{
                    this.sellerSCroll.refresh();
                }
            })
		},
      
       components:{
			Star,
			shopCar
		},
        methods:{
			_initPicture(){
				if( this.seller.pics){
					let pw = 120;
					let m = 12;
					let w = ( pw + m ) * this.seller.pics.length - m;
					this.$refs.piclist.style.width = w + 'px';

					if( !this.picBScroll ){
						this.picBScroll = new BScroll( this.$refs.picwrap ,{
							scrollX : true ,
							eventPassthrough : 'vertical'
						})
					}else{
						this.picBScroll.refresh();
					}
				}

			}
		}
    }
</script>
<style lang='less' scoped>
    .content{
        position: absolute;
		top: 3.6rem;
		bottom: 1rem;
		z-index: -99;
        
        .topone{
            box-sizing: border-box;
            margin-left:0.36rem;
            border-bottom:1px solid rgba(7,17,27,0.1);
            box-sizing: border-box;
            width:6.8rem;
            height: 1.8rem;
            display:flex;
            .topone-left{
                // border:1px solid red;
                height:100%;
                flex:1.7;
                .sellername{
                    font-size: 0.28rem;
                    line-height: 0.28rem;
                    color:rgb(7,17,27);
                    margin-top:0.36rem;
                   
                }
                .starts{
                    box-sizing: border-box;
                    height: 0.37rem;
                    width:2.15rem;
                    display:inline-block;
                    margin-top:0.16rem;
                    
                    img{
                        width:0.36rem;
                        height:0.36rem;
                    }
                    
                }
                .total{
                    box-sizing: border-box;
                    display:inline-block;
                    vertical-align: top;
                    margin-top:0.25rem;
                    margin-left:0.16rem;
                    font-size: 0.2rem;
                }
                .monthtotal{
                    box-sizing: border-box;
                    vertical-align: top;
                    margin-top:-0.25rem;
                    display:inline-block;
                    margin-left:3.1rem;
                    font-size: 0.2rem;
                    color:rgb(77,85,93);
                }
            }
            .topone-right{
                height:100%;
                flex:1;
                .love{
                    box-sizing: border-box;
                    margin-left:1.5rem;
                    margin-top:0.36rem;
                    width:0.5rem;
                    height:0.5rem;
                    img{
                        width:0.48rem;
                        height:0.48rem;
                    }
                }
                .tackcare{
                    // border:1px solid red;
                    box-sizing: border-box;
                    margin-left:1.4rem; 
                    margin-top:0.08rem;                   
                    width:0.8rem;
                    height:0.5rem;
                    span{
                        line-height:0.2rem;
                        font-size: 0.2rem;
                        color:rgb(77,85,93);
                    }
                }
            }
        }
        .toptwo{
            border-bottom:1px solid rgba(7,17,27,0.1);
            box-sizing: border-box;
            width:105%;
            height:1.48rem;
            display:flex;
            .toptwo-item{
                box-sizing: border-box;
                flex:1;
                text-align: center;
                border-left:1px solid rgba(7,17,27,0.1);
                font-size:0.2rem;
                color:rgb(147,153,159);
                margin-top:0.36rem;
               p{
                    margin-top:0.08rem;
                    line-height:0.2rem;
               }
               span{
                   font-size:0.48rem;
                   font-weight:200;
                   color:rgb(7,17,27);
                   line-height:0.48rem;
               }
            }
        }
        .whites1{
            // border:1px solid red;
            width:105%;
            height: 0.35rem;
            background-color: rgb(244,245,247);
        }
        .active{
            box-sizing: border-box;
            margin:0 auto;
            width:6.8rem;
            .titles{
                margin-top:0.36rem;
                font-size:0.28rem;
            }
            .textcontent{
                box-sizing: border-box;
                width:6.32rem;
                margin-top:0.16rem;
                margin-left:0.24rem;
                font-size:0.24rem;
                font-weight:200;
                color:rgb(240,20,20);
                line-height:0.48rem;
                margin-bottom:0.32rem;
                text-overflow: ellipsis;
                overflow: hidden;
            }
            .turn{
                // border:1px solid red;
                border-top:1px solid rgba(7,17,27,0.1);
                box-sizing: border-box;
                width:6.32rem;
                height:0.96rem;
                margin:0 auto;
                img{
                    width:0.35rem;
                    height:0.35rem;
                    margin-left:0.24rem;
                    margin-top:0.32rem;
                }
                p{
                   margin-left:0.12rem;
                   margin-top:0.32rem;
                   vertical-align: top;
                   display:inline-block; 
                   font-size:0.24rem;
                   color:rgb(7,17,27);
                   font-weight: 200;
                   line-height:0.32rem;
                }
            }
        }
        .whites2{
            width:105%;
            height: 0.35rem;
            background-color: rgb(244,245,247);
        }
        .relpic{
			width: 100%;
			padding: 0.36rem 0 0.36rem 0.36rem;
			.title{
				padding-bottom: 0.24rem;
				font-size: 0.36rem;
			}
			.pic-wrapper{
				width: 6.6rem;
				white-space: nowrap;
				overflow: hidden;
				.pic-item{
					display: inline-block;
					margin-right: 0.12rem;
					&:last-child{
						margin: 0;
					}
				}
			}
		}
        .whites3{
            width:105%;
            height: 0.35rem;
            background-color: rgb(244,245,247);
        }
        .sellinfo{
            margin:0 auto;
            width:6.8rem;
            div{             
                border-bottom:1px solid rgba(7,17,27,0.1);
                box-sizing: border-box;
                width:6.32rem;
                height:0.96rem;
                margin:0 auto;
                text-overflow: ellipsis;
                white-space: nowrap;
                overflow: hidden;
                p{
                   margin-left:0.12rem;
                   margin-top:0.32rem;
                   vertical-align: top;
                   display:inline-block; 
                   font-size:0.24rem;
                   color:rgb(7,17,27);
                   font-weight: 200;
                   line-height:0.32rem;
                }
            }
            .infotitle{
                font-size: 0.28rem;
                line-height: 0.28rem;
                color:rgb(7,17,27);
                margin-top:0.36rem;
                text-overflow: ellipsis;
                white-space: nowrap;
                overflow: hidden;
            }
        }
        .shopcar{
            position: fixed;
            bottom:0;
            left:0px;
            width:7.5rem;
            height:1rem;
        }
    }
</style>



