<template>
	<transition enter-active-class = 'animated slideInLeft' leave-active-class = 'animated rollOut'>
	<div class="foodDetail" v-show='detailShow' id="cc" ref="ccc">
		<div>
				<div class="hides" @click='hideFoodsDetail'>
					<!-- 上面的大 图片 -->
					<div class="toppic">
						<img :src="food.image" alt="">
					</div>
					<!-- 菜名 -->
					<p class="names">{{food.name}}</p>
					<!-- 月销售量 -->
					<div class="mothsell">
						月售<span>{{food.sellCount}}</span>份
					</div>
					<!-- 好评率 -->
					<div class="goodrate">
						好评率&nbsp;<span>{{food.rating}}</span>%
					</div>
					<!-- 价格与购物车 -->
					<div class="topend">
						<div class="allprice">
							<span class="newdollor">￥</span>
							<span class="newprice">{{food.price}}</span>
							<div class="rightprice">
								<span class="olddollor">￥</span>
								<span class="oldprice">{{food.oldPrice}}</span>
							</div>
						</div>
						<div class="addcar">
							<div>
								<p>加入购物车</p>
							</div>
						</div>
					</div>
					<!-- 空白部分 -->
					<div class="whiteone"></div>
					<!-- 商品介绍 -->
					<div class="introduce">
						<p>商品介绍</p>
						<div class="introducetext">{{food.info}}</div>
					</div>
					<!-- 空白部分 -->
					<div class="whiteone"></div>
					<!-- 下中部分++++++ -->
					<div class="mids">
						<p class="bigtitle">商品评价</p>
						<div class="one">
							<div class="all">
								<p class="allone">全部</p>
								<p class="alltwo">88</p>
							</div>
							<div class="fellgood">
								<p class="fellgoodone">满意</p>
								<p class="fellgoodtwo">99</p>
								</div>
								<div class="fellbad">
								<p class="fellbadone">不满意</p>
								<p class="fellbadtwo">57</p>
								</div>
						</div>
						<div class="two">
							<div class="trues">
								<img src="/static/imgs/true.png" alt="">
							</div>
							<div class="secontent">只看有内容的评价</div>
						</div>
					</div>
            		<!-- 下中部分 +++++++enddd-->
					<!-- 循环部分 -->
					<div class="turn" v-for="(k,v) in food.ratings" :key='v'>						
						<div class="commenttop">
							<div class="commenttime">{{k.rateTime | dataFormat }}</div>
							<div class="commentright">
								<div class="username">{{k.username}}</div>
								<div class="userpic">
									<!-- <img src="/static/imgs/decrease.png" alt=""> -->
									<img :src="k.avatar" alt="">
								</div>
							</div>
						</div>
						<div class="commentbtn">
							<div class="goodorbad">
								<img src="/static/imgs/good.png" alt="">
							</div>
							<div class="commenttext">{{k.text}}</div>
						</div>
					</div>
					<!-- 循环部分++++ -->		
				</div>
		</div>
		 <!-- 购物车固定在底部 -->
       <div class="shopcar">
           <shopCar></shopCar>
       </div>
       <!-- 购物车固定在底部end+++++++ -->
	</div>
</transition>
</template>
<script>
	import { mapGetters } from 'vuex'
	import animate from 'animate.css'
	import BScroll from 'better-scroll'
	import shopCar from '../shopcar/shopcar.vue'
	export default {

		computed : {
			...mapGetters([
				'detailShow',
				'food'
			])
		},
		components:{
			shopCar
		},
		mounted(){
          
			// console.log(5)
			if( !this.sellerSCroll ){
				// console.log(45)
				this.sellerSCroll = new BScroll( this.$refs.ccc,{
					click:true
				});
			}else{
				this.sellerSCroll.refresh();
			}
		},
		methods:{
			hideFoodsDetail(){
				console.log(0);
				this.$store.dispatch('hideFoodsDetail');
			}
		},
		filters: {  
            dataFormat: function (value) { 
                var time = new Date(value);

                var y = time.getFullYear();//年

                var m = time.getMonth() + 1;//月

                var d = time.getDate();//日

                var h = time.getHours();//时

                var mm = time.getMinutes();//分
                // console.log(y+"-"+m+"-"+d+" "+h+":"+mm);
            return y+"-"+m+"-"+d+" "+h+":"+mm;
            }  
        } 
	}
</script>

<style lang='less' scoped>
	.foodDetail{
		/*暂时这样让其滚动写页面*/
		// height:13.3rem;
		// overflow:scroll;
		position: fixed;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		background: white;
		width: 100%;
		.hides{
			.toppic{
				// border:1px solid red;
				width:100%;
				height:7.5rem;
				img{
					width:100%;
					height:7.5rem;
				}
			}
			.names{
				box-sizing: border-box;
				font-size:0.28rem;
				color:rgb(7,17,27); 
				font-weight: 700;
				line-height: 0.28rem;
				margin:0.36rem auto 0.16rem 0.36rem;
			}
			.mothsell{
				box-sizing: border-box;
				display: inline-block;
				margin-left:0.36rem;
				font-size: 0.2rem;
				color:rgb(147,153,159);
				line-height: 0.2rem;
			}
			.goodrate{
				box-sizing: border-box;
				display: inline-block;
				margin-left:0.24rem;
				font-size: 0.2rem;
				color:rgb(147,153,159);
				line-height: 0.2rem;
			}
			.topend{
				border-bottom:1px solid rgba(7,17,27,0.1);
				box-sizing: border-box;
				height:1rem;
				margin-top:0.36rem;
				.allprice{					
					margin-left:0.36rem;
					box-sizing: border-box;
					display: inline-block;
					.newdollor{
						font-size:0.28rem;
						font-weight: normal;
					}
					.newprice{
						font-size:0.28rem;
						color:red;
						font-weight: 700;
					}
					.rightprice{
						margin-left: 0.24rem;						
						box-sizing: border-box;
						display: inline-block;
						.olddollor{
							text-decoration:line-through;
							font-size:0.2rem;
							color:rgb(147,153,159);
						}
						.oldprice{
							text-decoration:line-through;
							font-weight: 700;
							font-size:0.2rem;
							color:rgb(147,153,159);
						}
					}
				}
				.addcar{
					background-color:rgb(0,160,220);
					box-sizing: border-box;
					display: inline-block;
					vertical-align: top;
					margin-left:3rem;
					width:1.48rem;
					height:0.48rem;
					border-radius: 0.24rem;
					div{						
						height:100%;
						margin:0 auto;
						display: table;
						p{
							line-height: 0.24rem;
							color:rgb(255,255,255);
							font-size:0.2rem;
							display:table-cell;
							vertical-align: middle;
						}
					}
				}
			}
			.whiteone{
				width:100%;
				height:0.32rem;
				background-color:#f3f5f7;
			}
			.introduce{
				box-sizing: border-box;
				border-bottom:1px solid rgba(7,17,27,0.1);
				width:100%;
				height:2.5rem;
				p{
					font-size: 0.24rem;
					margin-top:0.36rem;
					margin-left:0.25rem;
				}
				.introducetext{
					margin:0 auto;
					margin-top:0.12rem;
					box-sizing: border-box;
					width:6.9rem;
					height:2rem;
					font-size: 0.24rem;
					line-height: 0.48rem;
					text-overflow: ellipsis;
					// white-space: nowrap;
					overflow: hidden;
				}
			}
			 .mids{
				width:100%;
				height:2.8rem;
				border:1px solid rgba(7,17,27,0.1);
				.bigtitle{
					font-size: 0.24rem;
					margin-top:0.36rem;
					margin-left:0.3rem;
				}
				.one{
					// border:1px solid red;
					margin-left:-0.37rem;
					width:5rem;
					height:1.35rem;
					display: flex;
					div{
						// border:1px solid red;
						border-radius: 0.04rem;
						flex:1;
						margin-top:0.36rem;
						margin-left:0.16rem;
						width:1.5rem;
						height:0.6rem;
					}
					.all{
						background-color: rgb(0,160,220);
						line-height: 0.24rem;
						margin-left: 0.8rem;
						.allone{
							display:inline-block;
							// border:1px solid red;
							font-size:0.25rem;
							margin-top:0.15rem;
							margin-left:0.1rem;
						}
						.alltwo{
							display:inline-block;
							// border:1px solid red;
							font-size:0.24rem;
							margin-top:0.1rem;
							margin-left:0.1rem;
						}
					}
					.fellgood{
						background-color: rgb(206,234,247);
						.fellgoodone{
							display:inline-block;
							font-size:0.25rem;
							margin-top:0.15rem;
							margin-left:0.15rem;
						}
						.fellgoodtwo{
							display:inline-block;
							// border:1px solid red;
							font-size:0.24rem;
							margin-top:0.15rem;
							margin-left:0.15rem;
						}
					}
					.fellbad{
						background-color: rgb(234,235,237);
						.fellbadone{
							display:inline-block;
							font-size:0.25rem;
							margin-top:0.15rem;
							margin-left:0.1rem;
						}
						.fellbadtwo{
							display:inline-block;
							// border:1px solid red;
							font-size:0.24rem;
							margin-top:0.15rem;
							// margin-left:0.15rem;
						}
					}
				}
				.two{
					
					margin:0 auto;
					border-top:1px solid rgba(7,17,27,0.1);
					width:6.8rem;
					height:3rem;
				
					.trues{
					
						display:inline-block;
						border:1px solid rgb(176,179,180);
						margin-left:0.4rem;
						margin-top:0.14rem;
						border-radius: 0.4rem;
						width:0.4rem;
						height:0.4rem;
						background-color:rgb(176,179,184);
						img{
							width:0.36rem;
							height:0.36rem;                                  
						}
					}
					.secontent{
						// border:1px solid red;
						display:inline-block;
						width:4rem;
						height:0.4rem;
						line-height: 0.36rem;
						font-size: 0.24rem;
						color:rgb(147,153,159);
						margin-left:0.3rem;
						margin-top:0.3rem;
						
					}
				}
			}
			.turn{
				border-bottom:1px solid rgba(7,17,27,0.1);
				width:6.9rem;
				height: 2rem;
				margin:0 auto;
				.commenttop{
					.commenttime{
						
						box-sizing:border-box;
						display: inline-block;
						font-size:0.2rem;
						color:rgb(145,153,159);
						line-height:0.24rem;
						margin-top:0.32rem;
					}
					.commentright{
						
						box-sizing:border-box;
						display: inline-block;
						margin-left:4.1rem;
						.username{
						
							box-sizing:border-box;
							display: inline-block;
							width:1.3rem;
							height:0.24rem;
							font-size: 0.2rem;
							line-height: 0.24rem;
							text-overflow: ellipsis;
							// white-space: nowrap;
							overflow: hidden;
						}
						.userpic{
							
							box-sizing:border-box;
							display: inline-block;
							width:0.34rem;
							height: 0.34rem;
							margin-left:0.12rem;
							vertical-align: top;
							img{
								width:0.3rem;
								height: 0.3rem;
								border-radius: 0.3rem;
							}
						}
					}
					
				}
				.commentbtn{
					.goodorbad{

							box-sizing:border-box;
							display: inline-block;
							width:0.48rem;
							height:0.48rem;
							img{
								width:0.4rem;
								height:0.4rem;
								margin-top:0.05rem;
							}
					}
					.commenttext{
						
							box-sizing:border-box;
							display: inline-block;
							vertical-align: top;
							margin-left:0.08rem;
							width:4.3rem;
							height:0.48rem;
							font-size: 0.24rem;
							color:rgb(7,17,27);
							line-height:0.48rem;
							text-overflow: ellipsis;
							// white-space: nowrap;
							overflow: hidden;
					}
				}
			}
		}
		.shopcar{
            // border:1px solid red;
            position: fixed;
            bottom:0;
            left:0px;
            width:7.5rem;
            height:1rem;
           
        }
	}
</style>


