import axios from 'axios';
import TYPES from '../types.js'

const state = {
	ratings : []
	
}

const getters = {

	ratings( state ){
		return state.ratings;
	}

	// sellerShow( state ){
	// 	return state.sellerShow;
	// },
}

const actions = {
	
	getRatings( {commit} ){
		axios.get('data.json').then( res => {
			// console.log(res.data.seller);
			commit( TYPES.GET_RATINGS , res.data.ratings );
		})
	}

	// showDetail( { commit } ){

	// 	commit ( TYPES.SHOW_DETAIL );
	// },

	// hideDetail( { commit } ){
	// 	commit ( TYPES.HIDE_DETAIL );
	// }
}

const mutations = {
	
	[TYPES.GET_RATINGS](state,data){
		state.ratings = data;
	}

	// [TYPES.SHOW_DETAIL]( state ){
	// 	state.sellerShow = true;
	// },

	// [TYPES.HIDE_DETAIL]( state){
	// 	state.sellerShow = false;
	// }
}

export default {
	state,
	getters,
	mutations,
	actions
}


