                                </div>
                                <div class="score">{{seller.serviceScore}}</div>
                            </div>
                            <div class="rig two">
                                <span>服务态度</span>
                                <div class="star">
                                   <Star :score='seller.serviceScore' :size="'small'" class="littlestar"></Star>
                                </div>
                                <div class="score">{{seller.foodScore}}</div>
                            </div>
                            <div class="rig three">
                                <span>送达时间</span>
                                <div class="times">
                                {{seller.deliveryTime}}分钟
                                </div>
                            </div>
                    </div>
                    
                </div>
            </div>
            <!-- 上部分 endddd++++++++-->

            <!-- 空白部分 ++++++++-->
            <div class="whites"></div>
            <!-- 空白分 enddd++++-->

            <!-- 下中部分++++++ -->
            <div class="mids">
                <div class="one">
                    <div class="all">
                        <p class="allone">全部</p>
                        <p class="alltwo">{{ratings.length}}</p>
                    </div>
                    <div class="fellgood">
                        <p class="fellgoodone">满意</p>
                        <p class="fellgoodtwo">{{ratings.length}}</p>
                        </div>
                        <div class="fellbad">
                        <p class="fellbadone">不满意</p>
                        <p class="fellbadtwo">57</p>
                        </div>
                </div>
                <div class="two">
                    <div class="trues">
                        <img src="/static/imgs/true.png" alt="">
                    </div>
                    <div class="secontent">只看有内容的评价</div>
                </div>
            </div>
            <!-- 下中部分 +++++++enddd-->

            <!-- 循环部分底部 评价详情++++++-->
            <div class="bt" v-for="(v,k) in ratings" :key='k'>
                <div class="return1">
                    <!-- 头像 -->
                    <div class="one">
                        <div class="peoplepic">
                                <img :src="v.avatar" alt="">
                            </div>
                            <div class="oneright">
                                <p class="names">{{v.username}}</p>
                                <div class="times">{{v.rateTime | dataFormat }}</div>
                                <div class="stars">
                                    <Star :score='seller.serviceScore' :size="'small'" class="littlestar"></Star>
                                </div>
                                <p class="delivertime">{{v.deliveryTime}}分钟送达</p>
                            </div>
                    </div>
                    <!-- 文字描述 -->
